    .el-table th {
      padding: 8px 0;
    }
    .el-table .cell {
      line-height: 36px;
      text-align: center;
    }
  }

  // tab
  .sixth {
    .intro {
      font-size: 16px;
      color: #686868;
      margin-bottom: 20px;
    }
    .recommend {
      position: relative;
    }
    .safety {
      position: relative;
      border: 1px solid #ccc;
      border-radius: 3px;
      height: 248px;
      margin-bottom: 20px;
    }
    .ethic {
      position: relative;
    }
    .promise {
      position: relative;
      border: 1px solid #ccc;
      border-radius: 3px;
      height: 248px;
      margin-bottom: 20px;
    }
    .approve {
      position: relative;
    }
    .bigInput .el-textarea__inner {
      padding: 50px 15px 100px 40px;
      font-size: 16px;
    }
    // 
    @media screen and (max-width: 1600px) {
      .txt2 {
        bottom: 50px;
      }
      .txt21 {
        bottom: 50px;
      }
      .txt3 {
        bottom: 20px;
      }
    }
    // 
    @media screen and (min-width: 1601px) {
      .txt2 {
        bottom: 80px;
      }
      .txt21 {
        bottom: 80px;
      }
      .txt3 {
        bottom: 45px;
      }
    }
    .txt1 {
      position: absolute;
      left: 40px;
      top: 14px;
      z-index: 10;
      font-size: 18px;
      font-weight: 700;
    }
    .txt2 {
      position: absolute;
      right: 180px;
      // bottom: 80px;
      z-index: 10;
    }
    .txt21 {
      position: absolute;
      right: 50%;
      // bottom: 80px;
      z-index: 10;
    }
    .txt3 {
      position: absolute;
      right: 100px;
      // bottom: 45px;
      z-index: 10;
    }
    .txt4 {
      position: absolute;
      right: 88px;
      bottom: 10px;
      z-index: 10;
      font-size: 16px;
      color: #b3b3b3;
    }
    .txt5 {
      position: absolute;
      left: 40px;
      top: 60px;
      z-index: 10;
      padding-right: 100px;
      line-height: 40px;
      text-indent: 2em;
    }
  }
}
</style>





